import React, { Component } from 'react';
import ReactDOM from 'react-dom';
// import classNames from 'classnames';

const fourSeats = [
  'North',
  'East',
  'South',
  'West',
];

let imgLoaded = false;

const img = new Image();
img.onload = () => {
  imgLoaded = true;
};
img.src = '/public/images/poker.cards_.png';

class Room extends Component {
  static contextTypes = {
    router: React.PropTypes.object.isRequired,
  };

  constructor(props, context) {
    super(props, context);
    this.state = {
      name: 'None',
      owner: 'None',
      seats: [null, null, null, null], // north, east, south, west
      username: props.params.username,
      observers: [],
    };

    this.clickFollowing = this.clickFollowing.bind(this);
    this.clickFollowingHandler = this.clickFollowingHandler.bind(this);
    this.clickIndex = this.clickIndex.bind(this);
    this.clickIndexHandler = this.clickIndexHandler.bind(this);
    this.myCanvas = this.myCanvas.bind(this);
  }

  componentWillMount() {
    this.forceUpdate();
  }

  componentDidMount() {
    const thisArg = this;
    fetch(`/api/rooms/${this.props.params.roomname}`)
      .then((res) => {
        if (res.status === 404) {
          const { router } = this.context;
          router.push(`${this.props.params.username}/lobby`);
          return 'Not Found';
        } else {
          return res.json();
        }
      })
      .then((json) => {
        console.log('room: ', json);
        if (json === 'Not Found') {
          console.log('Error: Not Found but not 404...');
        } else {
          thisArg.setState(json);
        }
      })
      .catch((err) => {
        console.log('err', err);
      });
  }

  clickFollowing(name) {
    console.log('handleClick: ', name);
    const { router } = this.context;
    router.push(`/users/${name}`);
  }
  clickFollowingHandler(name) {
    function func() {
      this.clickFollowing(name);
    }
    func = func.bind(this);
    return func;
  }

  clickIndex(index) {
    this.setState({ index });
  }

  clickIndexHandler(index) {
    function func() {
      this.clickIndex(index);
    }
    func = func.bind(this);
    return func;
  }
  myCanvas() {
    if (imgLoaded) {
      const c = ReactDOM.findDOMNode(this.refs.myCanvas);
      const ctx = c.getContext('2d');

      ctx.drawImage(img, 0, 0, 200, 300, 0, 0, 200, 300);
    }
  }

  render() {
    return (
      <div>
        <p>{this.state.name}</p>
        <p>{`Username: ${this.state.username}`}</p>
        <p>{`Owner: ${this.state.owner}`}</p>
        <ul>
          {
            this.state.seats.map((seat, index) => {
              return <li><p>{`${fourSeats[index]}: ${seat}`}</p></li>;
            })
          }
        </ul>
        <p>Observers: </p>
        <ul>
          {
            this.state.observers.map((observer, index) => {
              return <li><p>{`${index}: ${observer}`}</p></li>;
            })
          }
        </ul>
        <img src='localhost:3000/public/images/poker.cards_.png' />
        <canvas ref="myCanvas" width="200" height="300">
          Your browser does not support the HTML5 canvas tag.
        </canvas>
        <p><button onClick={this.myCanvas}>Try it</button></p>
      </div>
    );
  }
}

export default Room;



